<template>

    <div class="conteudo">
     

      <div class="container" style="margin-top: 10px">
        <div class="row"><h1>Funcionalidades PWA</h1></div>
        <div class="row">
          <button class="btn btn-default" @click.prevent="vibrateSimple()">Vibrate for 200 ms</button>
          <button class="btn btn-default" @click.prevent="vibratePattern()">Vibrate with pattern</button>
        </div>
        <div class="row">
          <div class="col-sm-12">
            <p><button type="button" class="btn btn-lg btn-default" @click.prevent="getStream('video')">Grab video</button></p>
            <video controls autoplay style="height:180px; width: 240px;"></video>
          </div>
          <div class="col-sm-12">
            <p><button type="button" class="btn btn-lg btn-default" @click.prevent="getStream('audio')">Grab audio</button></p>
            <audio controls autoplay></audio>
          </div>
        </div>
      </div>


    </div>

</template>

<script>
export default {

  name: 'app',

  data() {

    return {


    }

  },

  created() {


  },

  methods: {

    vibrateSimple() {
      window.navigator.vibrate(200);
    },

    vibratePattern() {
      window.navigator.vibrate([100, 200, 200, 200, 500]);
    },

    getUserMedia(options, successCallback, failureCallback) {
      
      var api = navigator.getUserMedia || navigator.webkitGetUserMedia ||
      navigator.mozGetUserMedia || navigator.msGetUserMedia;
      
      if (api) {
        return api.bind(navigator)(options, successCallback, failureCallback);
      }
    },

    getStream(type) {

      if (!navigator.getUserMedia && !navigator.webkitGetUserMedia && !navigator.mozGetUserMedia && !navigator.msGetUserMedia) {
        alert('User Media API not supported.');
        return;
      }

      let constraints = {};
      constraints[type] = true;

      this.getUserMedia(constraints, function (stream) {

        let mediaControl = document.querySelector(type);

        if ('srcObject' in mediaControl) {

          mediaControl.srcObject = stream;
          mediaControl.src = (window.URL || window.webkitURL).createObjectURL(stream);

        } else if (navigator.mozGetUserMedia) {

          mediaControl.mozSrcObject = stream;

        }

      }, function (err) {

        alert('Error: ' + err);

      });
      
    }


  }


}
</script>

<style scoped>
  
</style>


