<template>
    <div class="conteudo">


      <section class="container-fluid manchete-modelo1">
        <ul class="lista row">
          <li v-for="noticia of noticias" class="col-xs-12 col-sm-4 col-md-6">
              <div class="item-lista">
                  <!-- <a :href="noticia.url"> -->
                    <router-link v-bind:to="'/interna/' + noticia.id_content">
                      <figure class="miniatura" v-if="getFoto(noticia.serialized.dados.foto[0].arquivo)">
                          <img :src="getFoto(noticia.serialized.dados.foto[0].arquivo)" class=" lazyloaded" style="width: 100%; height: 100%;">
                      </figure>
                      <span class="cartola vidaearte">{{ noticia.serialized.dados.titulo_categoria }}</span>
                      <h2 class="subdestaque">{{ noticia.titulo }}</h2>
                    </router-link>
                  <!-- </a> -->
              </div>
          </li>
        </ul>
      </section>


    </div>
</template>

<script>
import mixin from '../mixins/mixin.js';

export default {

  mixins: [mixin],

  data() {

    return {

      noticias: []

    }

  },

  created() {


    // Realiza a requisicao com o Vue Resource
    let promise = this.$http.get('https://www.opovo.com.br/apps,186,12/listagem-de-noticias', {
      params:{
        'hashtree:list': "42b1e15ada53e478dda089604a9ca8e8",
        'limit': 10,
        'offset': 12,
        'fulldata': true
      }
    });

    // Retorna uma promessa da Requisicao acima
    promise.then(

      // Caso a requisicao tenha sucesso
      res => {

        // Atualiza a Template com os dados do JSON
        this.noticias = res.body.ok;

        // Registra uma variavel no localStorage armazenando o JSON
        res.json().then(noticias => {
          localStorage.setItem('localNoticias', JSON.stringify(res.body.ok));
        });

      }, 

      // Caso a requisicao dê errado
      err => {

        // Show erro console
        console.log(err);

        // Carrega os JSON da ultima requisicao
        if(localStorage.getItem('localNoticias')){
          let localNoticias = localStorage.getItem('localNoticias');
          this.noticias = JSON.parse(localNoticias);
        }

      }

    );


  }


}
</script>

<style scoped>
  .conteudo {
    min-height: 600px;
  }
</style>



