// Funcoes Mixins podem ser usadas em qualquer componete
export default {

	created() {

	},
	
	methods: {

	    // Realiza os ajustes necessarios para exibir a imagem
	    getFoto(foto){

			let urlBase = 'https://www.opovo.com.br/vidaearteimages/';
			let removeNS = foto.slice(6);
			let urlFoto = urlBase + removeNS;
			return urlFoto;
			
	    },

	    backTopPage() {
	    	window.scrollTo(0, 0);
	    }

	}



}


